<template>
  <div class="product__counter form__counter">
    <button type="button" aria-label="Убрать один товар" @click="counterDecrease">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="amount" @input="counterChange(+$event.target.value)">

    <button type="button" aria-label="Добавить один товар" @click="counterIncrease">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
  export default {
    model: {
        prop: 'amount',
        event: 'update-amount'
    },
    props: ['amount'],
    methods: {
        counterDecrease() {
            if (this.amount > 1) {
              this.$emit("update-amount", this.amount - 1);
            }
        },
        counterIncrease() {
            this.$emit("update-amount", this.amount + 1);
        },
        counterChange(value) {
            this.$emit("update-amount", value);
        },
    },
  }
</script>