<template>
  <Counter v-model="currentAmount" :amount="currentAmount"/>

</template>

<script>
  import Counter from "@/components/Counter"

  export default {
    props: ['productAmount'],
    components: {Counter},
    computed: {
      currentAmount:{
        get(){
          return this.productAmount
        },
        set(value){
          this.$emit('update:productAmount', value)
        }
      }
    }
  }
</script>